import ButtonItems from '../../components/ButtonItems.vue'
import topBar from '../../components/TopBar.vue'
import store from '../../store/index.js'	

export default {
	data() {
        return {
            pickerHidden: true,
            chosen: '',
			submitOpen: false,
			Certificates: '身份证',
			data:{
				fullname: '',
				idNumber: '',
				mobile:'',
				cardNumber:'',
				callbackUrl:`${this.utils.getBaseUrl()}/#/pages/common/success`
			}
        }
    },
	computed:{
		submitBut: function(){
			return this.submitOpen ? 'big-blue' : 'big-gray'
		}
	},
	components:{
		ButtonItems,
		topBar
	},
    methods: {
		inputHandle: function (event) {
			this.data[event.target.id] = event.target.value
			
			const {fullname,idNumber,mobile,cardNumber} = this.data
		    if (fullname != '' && idNumber.length == 18 && mobile.length == 11 && cardNumber.length >= 16 ){
			   this.submitOpen = true
		    }
		},
		submitHandle: function () {
			this.request({
				url: 'consumer/my/consumers',
				method: 'POST',
				params: {...this.data}
			}).then(res => {
				store.commit('setOpenStorageData', res.data.result)
				//window.location.href="../../confirmStorage.html"
			}).catch(err => {
				uni.showToast({
					icon: 'none',
					title: '数据获取失败，重新登录！'
				});
			})
		}
    }
}
